.section {
    padding: 120px 0;
    display: flex;
    flex-direction: column;
    gap: 30px;
    align-items: center;
    justify-content: center;
    background-color: #0bb3c9;
}

.header {
    color: #ffffff;
    text-shadow: rgb(20, 20, 20) 2px 4px;
    font-family: "Roboto",sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 28px;
    line-height: 1.14;
    letter-spacing: .02em;
    @media screen and (min-width: 480px){
        font-size: 30px;
    }
    @media screen and (min-width: 768px){
        font-size: 42px;
    }
}

.text {
    color: #ffffff;
    font-family: "Roboto",sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 10px;
    line-height: 1.14;
    letter-spacing: .02em;
    @media screen and (min-width: 480px){
        font-size: 12px;
    }
    @media screen and (min-width: 768px){
        font-size: 20px;
    }
}

.form {
    display: flex;
    gap: 10px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.label {
    display: flex;
    gap: 10px;
    align-items: center;
}

.paragraph {
    width: 70px;
    color: #ebe5e5;
    font-family: "Roboto",sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.14;
    letter-spacing: .02em;
    @media screen and (min-width: 480px){
        width: 100px;
        font-size: 20px;
    }
    @media screen and (min-width: 768px){
        width: 140px;
        font-size: 28px;
    }    
}

.input {
    padding: 3px;
    background-color: transparent;
    outline: none;
    border: none;
    border-bottom: 1px solid #ebe5e5;
}

.button {
    display: block;
    padding: 5px;
    color: #ebe5e5;
    font-family: "Roboto",sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 1.14;
    letter-spacing: .02em;
    border: 2px solid #ebe5e5;
    border-radius: 2px;
    background-color: transparent;
    margin-top: 20px;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:hover {
        color: #ffffff;
        border-color: #ffffff;
        transform: scale(1.1);
    }
    @media screen and (min-width: 480px){
        font-size: 22px;
    }
    @media screen and (min-width: 768px){
        font-size: 26px;
    }
}